* {
	font-family: 'Montserrat', sans-serif;
}
html {
    font-size: 1rem;
	background-color: #fff;
	color: #000;
	text-align: center;
}
.toggle {
	cursor: pointer;
	font-size: 3rem;
}

.toggle .icon {
	font-size: 3rem;
}

footer {
	padding-top: 5px;
	font-size: 0.70rem;
	font-weight: 500;
}
.settings {
    font-weight: 700;
	padding: 10px;
	border-radius: 10px;
	border: 5px solid #000;
}
.settings #pollInterval {
	width: 3rem;
}
.settings #maxDistance {
	width: 4rem;
}

